import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Color } from './color.model';

/**
 * Padrões de tamanhos aceitos pelo componente do ícone
 */
export type IIconSize = 'S' | 'M' | 'L';

@Component({
  selector: 'app-color',
  templateUrl: './color.component.html',
  styleUrls: ['./color.component.scss']
})
export class ColorComponent implements OnInit {

  @Input() color: Color;
  @Input() size: IIconSize = 'M';
  @Input() disabled: boolean;
  @Input() showCheckIcon: boolean = true;

  @Output() selected = new EventEmitter();

  constructor(private sanitizer: DomSanitizer) { }

  ngOnInit() { }

  getColorBg(color: Color) {
    let bgStyle = '';

    if (color) {
      if (!color.hex2) {
        bgStyle = `${color.hex1}`;
      } else {
        bgStyle = `linear-gradient(180deg, ${color.hex1} 50%, ${color.hex2} 50%)`;
      }
    }

    return color ? this.sanitizer.bypassSecurityTrustStyle(bgStyle) : null;
  }

  setColor(color) {
    this.selected.emit(color);
  }

  get disabledPointer() {
    return this.disabled ? 'none' : '';
  }

  get width() {
    switch (this.size) {
      case 'S': return '19px';
      case 'M': return '32px';
      case 'L': return '42px';
      default: return '32px';
    }
  }

  get height() {
    switch (this.size) {
      case 'S': return '19px';
      case 'M': return '32px';
      case 'L': return '42px';
      default: return '32px';
    }
  }
}
