import { Component, OnInit, Input, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { MediaService } from 'src/app/core/services/media.service';
import { MainSharedService } from 'src/app/util/shared.service';

import { SkeletonLoaderComponent } from '../../skeleton-loader/skeleton-loader.component';

@Component({
  selector: 'app-order-resume-payer',
  templateUrl: './order-resume-payer.component.html',
  styleUrls: ['./order-resume-payer.component.scss'],
  providers: [SkeletonLoaderComponent]
})
export class OrderResumePayerComponent implements OnInit {

  @Input() respFinanceiro: boolean = false;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (window.innerWidth < 768) {
      this.tableCellWidth = '100%';
      this.productCellWidth = '100%';
    } else if (window.innerWidth < 1025) {
      this.tableCellWidth = this.isSeller ? '13%' : '20%';
      this.productCellWidth = '35%';
    } else {
      this.tableCellWidth = this.isSeller ? '15%' : '22%';
      this.productCellWidth = '30%';
    }
  }

  private _reserva: any;
  @Input()
  set reserva(value: any) {
    value && value.Data ? value.Data = value.Data.replace(/-/g, '/') : '';
    this._reserva = value;
  }
  get reserva(): any {
    return this._reserva;
  }

  @Input() pedido: any;

  get color() {
    const hex1 = this.pedido && this.pedido.cor && this.pedido.cor.Hex1 ? this.pedido.cor.Hex1 : 'white';
    return {
      nome: this.pedido && this.pedido.cor && this.pedido.cor.Cor ? this.pedido.cor.Cor : '',
      hex1: hex1,
      hex2: this.pedido && this.pedido.cor && this.pedido.cor.Hex2 ? this.pedido.cor.Hex2 : hex1
    };
  }

  get carGear() {
    return this.pedido && this.pedido.frota ? `${this.pedido.frota.Cambio}` : '';
  }

  get carGearMobile() {
    if(this.pedido && this.pedido.frota){
      return this.pedido.frota.Cambio == 'manual' ? 'M' : 'A';
    }
    return '';
  }

  get carName() {
    return this.pedido && this.pedido.frota ? `${this.pedido.frota.Marca} ${this.pedido.frota.Modelo}` : '';
  }

  get carDescription() {
    return this.pedido && this.pedido.frota ? `${this.pedido.frota.Versao}` : '';
  }

  // Image Src
  get imageVehicle() {
    if(this.pedido.frota.Fipe){
      return this.getImageFipe(this.pedido.frota.Fipe);
    }
  }


  public isSeller: boolean = localStorage.getItem('choosenView') === 'seller';
  public tableCellWidth: string;
  public productCellWidth: string;
  public medias: [];

  constructor(
    private router: Router,
    private shared: MainSharedService,
    private mediaService: MediaService
  ) { }

  ngOnInit() {
    this.onResize(null);
    this.mediaService.getMedia('fipes-image').subscribe(res => {
      this.medias = res.data;      
    });  
  }

  getImageFipe(fipe: any){
    return this.shared.getSrcImage(fipe, this.medias);
  }

  redirectToOrder(orderId) {
    this.router.navigate([`/assinatura/sucesso/${orderId}`], { queryParamsHandling: 'merge' });
  }

  errorHandlerImg(event) {    
    event.target.src = "assets/img/vehicles/carro-nao-encontrado.png";
  }
}
