import { Component, OnInit, Input, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { SkeletonLoaderComponent } from '../skeleton-loader/skeleton-loader.component';

@Component({
  selector: 'app-pedido',
  templateUrl: './pedido.component.html',
  styleUrls: ['./pedido.component.scss'],
  providers: [SkeletonLoaderComponent]
})
export class PedidoComponent implements OnInit {

  @Input() respFinanceiro: boolean = false

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (window.innerWidth <= 560) {
      this.isMobile = true;
    } else {
      this.isMobile = false;
    }
  }

  private _pedido: any;
  @Input()
  set pedido(value: any) {
    this._pedido = value;
    this.setStatusDetails(value);
  }
  get pedido(): any {
    return this._pedido;
  }

  private _reserva: any;
  @Input()
  set reserva(value: any) {
    this._reserva = value;
    this.checkReservation(value);
  }
  get reserva(): any {
    return this._reserva;
  }

  public isMobile: boolean;
  public isSeller: boolean = localStorage.getItem('choosenView') === 'seller';
  public emptyReservation: boolean = false;
  public showRedirectBtn: boolean = false;
  public btnLabel = 'Continuar Pedido';
  public orderMsg = 'Clique no botão para continuar seu pedido.';

  constructor(
    private router: Router
  ) { }

  ngOnInit() {
    this.onResize(null);
  }

  checkReservation(reserva) {
    if (!reserva || (!reserva.length && reserva.length === 0)) {
      this.emptyReservation = true;
      this.orderMsg = 'Não foi possível exibir os detalhes do seu pedido.';
    }
  }

  setStatusDetails(pedido) {
    this.showRedirectBtn = (
      pedido && pedido.status && pedido.status.StatusId
      && (
        pedido.status.StatusId === 0
        || pedido.status.StatusId === 1
        || pedido.status.StatusId === 2
        || pedido.status.StatusId === 4
      )
    );
  }

  redirectTo() {
    if (
      this.pedido && this.pedido.status && this.pedido.status.StatusId
      && (
        this.pedido.status.StatusId === 0
        || this.pedido.status.StatusId === 1
        || this.pedido.status.StatusId === 2
        || this.pedido.status.StatusId === 4
      )
    ) {
      this.router.navigate([`/assinatura/pagamento/${this.pedido.PedidoFrotaID}`], { queryParamsHandling: 'merge' });
    }
  }
}
