import { Component, OnInit, Input } from '@angular/core';
//import { Store } from 'src/app/shared/models/order.model';

@Component({
  selector: 'app-local-retirada-payer',
  templateUrl: './local-retirada-payer.component.html',
  styleUrls: ['./local-retirada-payer.component.scss']
})
export class LocalRetiradaPayerComponent implements OnInit {

  @Input() pedido: any;
  @Input() reserva: any;

  // Data orders
  get loja() {
    return this.pedido && this.pedido.loja ? this.pedido.loja.Nome : '---'
  }

  get endereco() {
    return this.pedido && this.pedido.loja ? this.pedido.loja.Endereco.split('/')[0].trim() : '---'
  }

  get cidade() {
    return this.pedido && this.pedido.loja ? this.pedido.loja.Endereco.split('/')[1].split('-')[0].trim() : '---'
  }

  get estado() {
    return this.pedido && this.pedido.loja ? this.pedido.loja.Endereco.split('/')[1].split('-')[1].trim() : '---'
  }

  get prazoEntrega() {
    return this.pedido && this.pedido.frota ? this.pedido.frota.PrazoEntrega : '---'
  }

  constructor() { }

  ngOnInit() { }

  getDeliveryTime() {

    if (this.pedido) {
      const date1 = new Date(this.pedido.DataPedido);
      const date2 = new Date(this.pedido.DataPrevistaRetirada);

     // To calculate the time difference of two dates
      const Difference_In_Time = date2.getTime() - date1.getTime();

      // To calculate the no. of days between two dates
      return Math.round(Difference_In_Time / (1000 * 3600 * 24)) + 1;
    }
  }
}
