import { Component, Input, OnInit } from '@angular/core';
import { FormatNumber } from 'src/app/util/formatNumber.util';

@Component({
  selector: 'app-forma-pagamento-payer',
  templateUrl: './forma-pagamento-payer.component.html',
  styleUrls: ['./forma-pagamento-payer.component.scss']
})
export class FormaPagamentoPayerComponent implements OnInit {

  @Input() reserva: any;
  public totalInstallments: string;
  public totalInstallmentsPromotion: string;
  private readonly formatNumber: FormatNumber = new FormatNumber();

  constructor() { }

  ngOnInit() {
    this.calculateInstallments();
  }

  private calculateInstallments(): void {

    let previous_valor_mensal_total = 0;
    let previous_period = 0;
    let promotion: any = {
      has_promo: false
    };

    if (this.reserva && this.reserva.Periodos && this.reserva.Periodos.length > 0) {

      this.reserva.Periodos.forEach((periodo: any) => {
        if (periodo.valor_mensal_total != previous_valor_mensal_total && previous_period > 0) {
          promotion = {
            previous_valor_mensal_total: previous_valor_mensal_total,
            valor_mensal_total: periodo.valor_mensal_total,
            previous_period: previous_period,
            has_promo: true
          };
        }
        previous_valor_mensal_total = periodo.valor_mensal_total;
        previous_period++;
      });

      if (promotion.has_promo) {
        this.totalInstallmentsPromotion = `${promotion.previous_period}x de ${this.formatNumber.getBRLCurrency(Number(promotion.previous_valor_mensal_total))} mais ${this.reserva.Periodos.length - promotion.previous_period}x de ${this.formatNumber.getBRLCurrency(Number(promotion.valor_mensal_total))}`;
      } else {
        this.totalInstallments = `${this.reserva.Periodos.length}x de ${this.formatNumber.getBRLCurrency(Number(this.reserva.Periodos[0].valor_mensal_total))}`;
      }

    }
  }
}
